@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.footer_button_allow{
   position: relative;
   margin-left: 10px;
   &::before{
    content:"";
    position: absolute;
    top:10px;
    width: 10px;
    height: 2px;
    transform: rotate(-30deg);
    background-color: #fff;
   }
   &::after{
    content:"";
    position: absolute;
    top:10px;
    left:6px;
    width: 10px;
    height: 2px;
    transform: rotate(30deg);
    background-color: #fff;
   }
}